import React from "react"
import { Link, graphql } from "gatsby"
import css from "@emotion/css"

import Layout from "../components/Layout"

const pathRank = (path: string) => {
  return (path.match(/\//g) || []).length
}

const IndexPage = ({ data }) => {
  const { author } = data.site.siteMetadata

  const paths: string[] = data.allSitePage.edges
    .map(({ node }) => node.path)
    .sort((a, b) => {
      const aPath = pathRank(a)
      const bPath = pathRank(b)
      if (aPath === bPath) return 0
      return aPath > bPath ? 1 : -1
    })

  return (
    <Layout headProps={{ title: "" }}>
      <div
        css={css`
          max-width: 600px;
          margin: auto;
          padding: 1rem;
        `}
      >
        <h1>{author.shortName}'s notes</h1>
        {paths.map(path => (
          <p key={path}>
            <Link to={path}>{path}</Link>
          </p>
        ))}
      </div>
    </Layout>
  )
}

export default IndexPage

export const pageQuery = graphql`
  query HomePage {
    site {
      siteMetadata {
        author {
          shortName
        }
      }
    }
    allSitePage(filter: { isCreatedByStatefulCreatePages: { eq: false } }) {
      edges {
        node {
          path
        }
      }
    }
  }
`
